import React, { Component } from 'react';
import "./index.scss";
export default class index extends Component {
	constructor(props) {
		super(props);
		this.countDown = this.countDown.bind(this);
		this.state = {
			id: 0,
			d: "00",
			h: "00",
			m: "00",
			s: "00"
		};

		const dateString = props.time;
		const reggie = /(\d{4})-(\d{2})-(\d{2}) (\d{2}):(\d{2}):(\d{2})/;
  		const [, year, month, day, hours, minutes, seconds] = reggie.exec(dateString);
  		this.countDownDate = new Date(year, month-1, day, hours, minutes, seconds).getTime();
	}

	componentDidMount() {
		let id = setInterval(this.countDown, 1000);
		this.setState({id: id});
	}

	stop = () => {
		const {id} = this.state;
		clearInterval(id);
		this.setState({id: 0});
	}

	countDown = () => {
		let now = new Date().getTime();
	    let distance = this.countDownDate - now;
	    
	    let d = Math.floor(distance / (1000 * 60 * 60 * 24));
	    let h = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
	    let m = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
	    let s = Math.floor((distance % (1000 * 60)) / 1000);

	    if (distance < 0) {
	        this.stop();
	    }

		d = (d + "").trim().length === 1 ? "0" + d : d;
		h = (h + "").trim().length === 1 ? "0" + h : h;
		m = (m + "").trim().length === 1 ? "0" + m : m;
		s = (s + "").trim().length === 1 ? "0" + s : s;

		this.setState({
			d: d,
			h: h,
			m: m,
			s: s
		});
	}

	render() {
		const {id, d, h, m, s} = this.state;
		return (
			<div className="count-down">
				{id !== 0 ? <span>{d} : {h} : {m} : {s}</span> : <span>Орчуулж байна ...</span>}
			</div>
		);
	}
}



