import React, { Component } from 'react';
import "./style.scss";
export default class index extends Component {
	render() {
		const {url} = this.props;
		return (
			<div className="video">
				<iframe title={url} src={url} width="100%" height="480px" allowFullScreen={true} />
			</div>
		);
	}
}



