import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import QueueAnim from 'rc-queue-anim';
import './Header.scss';
import Menu from '../Menu';

export default class index extends Component {	
	render() {
		return (
			<div className="header">
				<QueueAnim type="top" className="row middle-xs">	
					<div key="header-anime-1" className="col-xs-12">
						<Link to="/">
							<h1 className="text-center logo">WhiteFox <sup>BETA V.1</sup></h1>
						</Link>
					</div>
				{/*
					<div key="header-anime-2" className="col-xs-12 col-sm-3">
						<input
							className="form-control"
							type="text"
							placeholder="Хайлт хийх" 
						/>
					</div>
					<div key="header-anime-3" className="col-xs-12 col-sm-6">
						{<Menu />
					</div>
				*/}
			    </QueueAnim>
		    </div>
		);
	}
}



