import { observable, action, autorun} from 'mobx';
import api from "../api";
import categoryStore from "./CategoryStore";

class Store {
	@observable isLoading = false;
    @observable posts = [];
    @observable post = {};
    @observable errors = null;
    @observable nextPageToken = "";
    @observable maxResult = 10;

    @observable animes = new Map();
   
    @action loadPosts() {
    	this.isLoading = true;
    	return api.Posts.fetchPosts(this.maxResult).then(action((res) => {
    		this.posts = res.items;
            this.nextPageToken = res.nextPageToken;
    	})).catch(action((e) => {
    		this.errors = e;
    	})).finally(action(() => {
            setTimeout(() => {
                this.isLoading = false;     
            }, 1000); 
        }));
    }

    @action loadMorePosts() {
        // this.isLoading = true;
        return api.Posts.fetchMorePosts(this.maxResult, this.nextPageToken)
        .then(action((res) => {
            // this.posts.replace(this.posts.concat(res.items));   
            res.items.forEach((post) => {
                this.posts.push(post);
            });

            this.nextPageToken = res.nextPageToken;
        })).catch(action((e) => {
            this.errors = e;
        })).finally(action(() => {
        }));
    }

    @action loadPost(id) {
        this.isLoading = true;             
        return api.Posts.fetchPost(id).then(action((post) => {
            this.post = post;
        })).catch(action((e) => {
            this.errors = e;
        })).finally(action(() => {
            setTimeout(() => {
                this.isLoading = false;     
            }, 1000);
        }));
    }

    @action pullAnime(label) {
        this.isLoading = true;
        return api.Posts.fetchFilterPosts(label)
        .then(action((posts) => {
            this.animes.set(label, posts.items);
        })).finally(action(() => {
            setTimeout(() => {
                this.isLoading = false;     
            }, 1000);
        }));
    }
}


const store = new Store();

export default store;


autorun(() => {
    console.log(store.animes);
});


