import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import './index.scss';
import QueueAnim from 'rc-queue-anim';
import CountDown from '../CountDown';


@inject("store")
@observer
export default class index extends Component {

	constructor(props) {
		super(props);
		this.regex = /^(\d{4})-(\d{2})-(\d{2}) (\d{2}):(\d{2}):(\d{2})$/;
		this.time = "";

		this.state = {
			episodeList: []
		};

	}

	componentWillMount() {
		const {index} = this.props;
		const {animes} = this.props.store;
		let tmp = [];
		animes.get(index).forEach((anime, index) => {
			if(index !== 0) {
				tmp.push(anime);
			}
		});

		this.setState({episodeList: tmp});
	}
	
	getTitle(title) {
		const index = title.indexOf("[");
		return index >= 0 ? title.substring(0, index) : title;
	}

	hasCountDown(title) {
		const start = title.indexOf("[");
		const end = title.indexOf("]");
		if(start < 0 || end < 0) {
			return false;
		}
		this.time = title.substring(start + 1, end);
		return this.regex.test(this.time);
	}


	renderEp() {
		const max = 5;
		let {episodeList} = this.state;
		episodeList = episodeList.reverse();
		let fix = [];
		if(episodeList.length < max) {
			for(let i = 0; i < max - episodeList.length; i++) {
				fix.push(i);
			}
		}

		return(
			<div className="episode">
				{episodeList && episodeList.map((ep, index) => (
					<Link to={`/anime/${ep.id}`}>
						{index + 1}
					</Link>
				))}
				{fix.map((ep) => (
					<Link to="" className="no-link">
						...
					</Link>
				))}
			</div>
		);
	}


	render() {
		const {content, index} = this.props;
		const hasCountDown = this.hasCountDown(content.title);

		return (
			<QueueAnim type="bottom">
				<div className={`post ${hasCountDown && 'has-countdown'}`} key={`post-${index}-anime`}>
					
					<div className="post-thumbnail">
						{((content.images && content.images[0]) && !hasCountDown) ?
							<Link to={`/anime/${content.id}`}>
								<img src={`${content.images[0].url}`} alt=""/>
							</Link> :
							<img src={`${content.images[0].url}`} alt=""/>
						}
						{this.renderEp()}
					</div>

					<div className="post-desc">
						<div className="post-title">
							{this.getTitle(content.title)}
						</div>
						<div className="post-footer">
							
						</div>
					</div>
				</div>
			</QueueAnim>
		);
	}
}



