import { client } from "./client";
const maxResults = 5000;
let url = {
	posts: "/posts.json",
	post: "/post.json",
	filter: "/posts-filter.json"
};


const mode = process.env.NODE_ENV;

if(mode !== "development") {
	url = {
		posts: "/posts",
		post: "/posts"
	};
}

export default {
	fetchPosts: (limit) => {
		return client.get(url["posts"], {
			params: {
				maxResults: limit,
				fetchImages: true,
				fetchBodies: false
			}
		})
		.then((res) => {
			return res.data;
		})
		.catch((e) => {
			return e;
		});
	},

	fetchMorePosts: (limit, nextPageToken) => {
		return client.get(url["posts"], {
			params: {
				maxResults: limit,
				pageToken: nextPageToken,
				fetchImages: true,
				fetchBodies: false
			}
		}).then((res) => {
			return res.data;
		}).catch((e) => {
			return e;
		});
	},

	fetchPost: (id) => {
		return client.get(url["post"] + "/" + id, {
			params: {
				fetchImages: true,
				fetchBodies: true
			}
		})
		.then((res) => {
			const sj = res.data.content.replace(/<\/?[^>]+(>|$)/g, "").replace(/(?:\r\n|\r|\n)/g, '');
			const content = JSON.parse(sj);
			res.data.custom = {...content};
			return res.data;
		})
		.catch((e) => {
			return e;
		});
	},

	fetchFilterPosts: (labels) => {
		return client.get(url.filter, {
			params: {
				maxResults: maxResults,
				fetchBodies: false,
				fetchImages: true,
				labels: labels
			}
		}).then((res) => {
			return res.data;
		})
		.catch((e) => {
			return e;
		});
	}
}


