import React, { Component } from 'react';

export default class index extends Component {
	constructor(props) {
		super(props);
		this.state = {
			currentIndex: 0
		};
	}

	handleOnClick(index) {
		this.setState({ currentIndex: index});
	}

	render() {
		const { header, content } = this.props;
		const { currentIndex } = this.state;
		return (
			<div className="tab">
				<div className="tab-header">
					{header.map((value, index) => (
						<li key={`tab-${index}`} onClick={() => this.handleOnClick(index)} className={currentIndex === index && 'active'}>
							{value}
						</li>
					))}
				</div>
				
				<div key="tab-content-anime" className="tab-content">
					{content[currentIndex]}
				</div>
			</div>
		);
	}
}



