import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import "./Menu.scss";

export default class index extends Component {
	render() {
		return (
			<div className="menu">
				<NavLink to="/" activeClassName="active">Аниме жагсаалт</NavLink>
				<NavLink to="/wtf" activeClassName="active">Үзэх аниме</NavLink>
			</div>
		);
	}
}



