import { observable, action, autorun} from 'mobx';
import api from "../api";

class CategoryStore {
	@observable isLoading = false;
    @observable category = [];

   
    @action pullCategory() {
    	this.isLoading = true;
    	return api.Category.fetchCategory().then(action((res) => {
    		this.category = res;
    	})).catch(action((e) => {
    		this.errors = e;
    	})).finally(action(() => {
            setTimeout(() => {
                this.isLoading = false;     
            }, 1000);
        }));
    }
}

const categoryStore = new CategoryStore();

export default categoryStore;


