import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { withRouter } from 'react-router-dom';

import QueueAnim from 'rc-queue-anim';

import "./style.scss";
import Tab from "../../components/Tab";
import Video from "../../components/Video";
import Loading from "../../components/Loading";

@inject('store')
@withRouter
@observer
export default class index extends Component {
	
	componentWillMount() {
		const {id} = this.props.match.params;
		this.props.store.loadPost(id);
	}
	
	componentDidMount() {
		window.scrollTo(0, 0);
	}

	renderOverview(overview) {
		const content = overview && JSON.parse(overview);
		return(
			<QueueAnim>
				<div key="overview-anime">
					Coming soon...
				</div>
			</QueueAnim>
		);
	}

	renderEpisodes() {
		return(
			<QueueAnim>
				<div key="episode-anime">
					Coming soon...
				</div>
			</QueueAnim>
		);
	}

	render() {
		const {post} = this.props.store;
		const {isLoading} = this.props.store;
		const data = post && post.custom && post.custom.data;

		return (
			<QueueAnim type="bottom">
				{isLoading || !data ? <Loading /> : (
					<div key="post-detail-anime" className="post-detail">
						<div className="post-detail-box">
							<div className="post-video">
								<Video url={data && data.video}/>
							</div>
							<div className="post-desc">
								<div className="post-small-thumbnail">
									{ (post.images && post.images[0]) && 
										<img src={`${post.images[0].url}`} alt=""/>
									}
								</div>
								<div className="post-title">
									<h2>{post && post.title}</h2>
									<p className="downloads">
										{data.links && data.links.map((link, index) => (
											<a href={link.url} target="_blank" className={`btn-gra g-${index + 1}`}>{link.name}</a>
										))}
									</p>
								</div>
								<div className="post-watched">
									{/*Үзсэн: <span className="label">{post.thr$total.$t}</span>*/}
									
								</div>
							</div>
						{/*
							<Tab 
								header={["Танилцуулга", "Ангийн жагсаалт"]} 
								content={[this.renderOverview(post && post.content), this.renderEpisodes() ]}
							/>
						*/}
						</div>
					</div>
				)}
			</QueueAnim>
		);
	}
}



