import { client } from "./client";
let url = {
	list: "/category.json"
};

const mode = process.env.NODE_ENV;

if(mode !== "development") {
	const BASE_URL = "https://" + window.location.host;
	url = {
		list: BASE_URL + "/feeds/posts/default"
	};
}


export default {
	fetchCategory: () => {
		return client.get(url.list, {
			params: {
				alt: "json",
				"start-index": 1, 
				"max-results": 0,
				orderby: "published"
			}
		})
		.then((res) => {
			const {category} = res.data.feed;
			const list = category.map((c) => {
				return c.term;
			});
			return list;
		})
		.catch((e) => {
			return e;
		});
	}
}


