import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { withRouter } from 'react-router-dom';

import "./index.scss";
import Post from "../Post";


@inject("store")
@observer
export default class index extends Component {
	constructor(props) {
		super(props);
		this.state = {
			anime: null
		}
	}

	componentWillMount() {
		const {label} = this.props;
		this.props.store.pullAnime(label).then(() => {
			const {animes} = this.props.store;
			this.setState({anime: animes.get(label)});
		});

	}

	render() {
		const {label} = this.props;
		const {anime} = this.state;
		let lastEp = anime && anime[0];


		/*{anime && anime.map((ep, index) => (
		<p>
		<code>{index + 1}: {ep.title}</code>
		</p>
		))}
		*/

		return (
			<div className="anime">
				{lastEp && <Post content={lastEp} index={label}/>}
			</div>
		);
	}
}



